import React, { useEffect, useState } from 'react';
import Navbar from '../sub/Navbar';
import Hero from '../sub/Hero';
import OurProducts from '../sub/OurProducts';
import OurStack from '../sub/OurStack';
import Footer from '../sub/Footer';
import { useDispatch } from 'react-redux';
import { setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import './Home.css';
import { COMPANY_NAME } from '../../../shared/utility';
import OurServices from '../sub/OurServices';
import { isMobile } from 'react-device-detect';
const defaultColor = 'white';//'transparent';// '#FCF9F5';
function Home() {
	const [background, setBackground] = useState(defaultColor)

	document.title = `Home | ${COMPANY_NAME}`;

	const dispatch = useDispatch();

	const handleNavigation = e => {
		const window = e.currentTarget;
		if (window.scrollY == 0) {
			setBackground(defaultColor)
		} else {
			setBackground('white')
		}
	};

	useEffect(() => {
		window.addEventListener('scroll', e => handleNavigation(e));
		window.scrollTo({ top: 0 });
	}, []);

	useEffect(() => {
		dispatch(setShowSideMenu(false));
		dispatch(setShowMenu(false));
	}, []);

	return (
		<div className="home-section">
			<Navbar background={background} />
			<Hero />
			<OurServices />
			{!isMobile &&
				<OurProducts />
			}
			<OurStack />
			<Footer />
		</div>
	);
}

export default Home;
